.loader-container {
  display: none;
}

.loader {
  width: 20px;
  height: 20px;
  border: 4px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 0 auto;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: red;
  margin-bottom: 10px;
  font-weight: bold;
  display: none;
}

.show-error {
  display: block;
}

